body {
  margin: 0em;
  font-family: Arial, Helvetica, sans-serif;
  letter-spacing: 0px;
  font-weight: normal;
  font-size: 1.2em;
}
p {
  margin: 1em auto;
}
.font-color-2 {
  color: #f82faf;
}
body .bg-0,
* .bg-0,
body .bg-0 *:not(a),
* .bg-0 *:not(a) {
  background-color: #F1F2F1 !important;
  color: #1b1c1d !important;
}
body .bg-1,
* .bg-1,
body .bg-1 *:not(a),
* .bg-1 *:not(a) {
  background-color: #1b1c1d !important;
  color: #F1F2F1 !important;
}
body .bg-3,
* .bg-3,
body .bg-3 *:not(a),
* .bg-3 *:not(a) {
  background-color: #34262b !important;
  color: #1b1c1d !important;
}
a {
  color: #f82faf;
  font-size: 1.2em;
  /*font-family: @font-main; */
  letter-spacing: 0px;
  font-weight: normal;
}
a:hover {
  color: #f82faf;
  text-decoration: underline;
}
h1.ui.header,
h1.ui.centered.header {
  font-size: 3.3em;
  font-family: 'cool';
  letter-spacing: 5px;
}
h1.ui.header.dark,
h1.ui.centered.header.dark {
  color: #1b1c1d;
}
h1.ui.header.bright,
h1.ui.centered.header.bright {
  color: #F1F2F1;
}
@media all and (max-width: 631px) {
  h1.ui.header,
  h1.ui.centered.header {
    font-size: 2.3em;
  }
}
.text-shadow {
  text-shadow: 0px 1px 3px black;
}
h2.ui.header,
h2.ui.centered.header {
  font-size: 3em;
  font-family: 'cool';
  letter-spacing: 2.5px;
  padding-top: 55px;
  padding-bottom: 20px;
}
h2.ui.header.dark,
h2.ui.centered.header.dark {
  color: #1b1c1d;
}
h2.ui.header.bright,
h2.ui.centered.header.bright {
  color: #F1F2F1;
}
@media all and (max-width: 631px) {
  h2.ui.header,
  h2.ui.centered.header {
    font-size: 2em;
  }
}
h3 {
  font-size: 26pt;
  padding: 15px 0 15px 0!important;
  font-family: 'cool';
  letter-spacing: 2.5px;
}
@media all and (max-width: 631px) {
  h3 {
    font-size: 25.2pt;
  }
}
.main-font-color {
  color: #F82FAF;
}
.bright,
.txt-white {
  color: #F1F2F1;
}
.brighth1,
.txt-whiteh1 {
  color: #F1F2F1;
}
.brighth2,
.txt-whiteh2 {
  color: #F1F2F1;
}
.ui.fixed.sticky + p {
  margin-top: 39px;
}
