  font-family: 'cool';
  src: url("../fonts/coolvetica.eot");
  src: url("../fonts/coolvetica.eot?#iefix") format('embedded-opentype'), url("../fonts/coolvetica.woff2") format('woff'), url("../fonts/coolvetica.woff") format('woff'), url("../fonts/coolvetica.ttf") format('truetype'), url("../fonts/coolvetica.svg") format('svg');
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-decoration: inherit;
  text-transform: none;
}
